.tb_gp_post_container{
    display: flex;
    flex-wrap: wrap;
    width: 100%;
}

.tb_gp_post_wrapper{
    width: 25%;
    padding: 7.5px;
    height: auto;
    display: inline-block;
}

.tb_gp_post_in{
    display: block;
    position: relative;
    cursor: pointer;
    box-shadow: 0 0 1.5px rgba(0, 0, 0, 0.20);
    overflow: hidden;
    background-color: var(--tb-bg-color);
    border-radius: var(--tb-border-radius);
}

.tb_gp_post_media_wrapp{
    position: relative;
}

.tb_gp_contant_ {
    position: absolute;
    height: 100%;
    width: 100%;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3;
    padding: 8px;
    opacity: 0;
    visibility: hidden;
    overflow: hidden;
    transition: opacity .3s ease, visibility .3s ease;
}

.tb_gp_contant__in{
    height: 100%;
    width: 100%;
    display: flex;
    padding: 15px;
    flex-direction: column;
    justify-content: center;
    position: relative;
}

.tb_gp_share_container{
    z-index: 4;
}

.tb_gp_post_overlay {
    background-color: rgba(0,0,0,1);
    height: 100%;
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    z-index: 0;
    opacity: .8;
}

.tb_gp_post_in:hover .tb_gp_contant_ {
    opacity: 1;
    visibility: visible;
}

.tb_gp_content__{
    z-index: 1;
    position: relative;
}

.tb_gp_contant_.tb_gp_hover_content_{
    padding: 0;
    height: 85%;
    top: 15%;
}

.tb_gp_hover_content_ .tb_gp_content__{
    transform: translateY(50%);
    transition: all .7s ease;
}

.tb_gp_post_in:hover .tb_gp_hover_content_ .tb_gp_content__{
    transform: translate(0);
}

.tb_gp_post_ani .tb_gp_post_overlay{
    transform: translateY(100%);
}

.tb_gp_post_in:hover .tb_gp_post_overlay{
    transform: translate(0);
}

.tb_gp_hover_content_ .tb_gp_post_overlay{
    opacity: 0;
    transition: all .7s ease;
}
.tb_gp_post_in:hover .tb_gp_hover_content_ .tb_gp_post_overlay{
    opacity: .8;
}


.tb_gp_content__ .tb_text_links_{
    color: inherit !important;
    text-decoration: underline !important;
    display: inline;
}

.tb_gp_contant_wrapper {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    margin: 0 auto;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 15px;
}

.tb_gp_image_wrap_{
    position: relative;
    padding-bottom: 100%;
}

.tb_gp_image{
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    width: 100% !important;
    height: 100% !important;
    display: block;
    object-fit: cover;
    object-position: center;
}

.tb_gp_author_profile {
    height: 46px;
    width: 46px;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    margin: 0 auto;
    margin-bottom: 12px;
    border: 1px solid #fff;
    background-color: rgba(0,0,0,.2);
    position: relative;
    overflow: hidden;
}
.tb_gp_author_profile__{
    height: 100% !important;
    width: 100% !important;
    margin: 0;
    object-fit: cover;
    object-position: center;
}
.tb_gp_post_size_2x .tb_gp_author_profile {
    height: 88px;
    width: 88px;
}

.tb_gp_authorname {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 4px;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-align: center;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_gp_authorname {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
}

.tb_gp_username,
.tb_gp_time {
    font-family: var(--tb-font);
    color: var(--tb-author-color);
}

.tb_gp_seprator {
    background-color: var(--tb-author-color);
}

.tb_gp_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
}

.tb_gp_author_info {
    overflow: hidden;
    display: flex;
    flex-shrink: 0;
    flex-direction: column;
    width: 100%;
    margin-bottom: 15px;
}

.tb_gp_post_info {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_gp_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_gp_username, .tb_gp_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_gp_username{
    max-width: 65%;
}

.tb_gp_time{
    max-width: 35%;
}

.tb_gp_post_info__ .tb_gp_time,
.tb_gp_post_info__ .tb_gp_username{
    max-width: 100%;
}

.tb_gp_post_wrapper_te .tb_gp_social_{
    opacity: 1;
}

.tb_gp_social_ {
    position: absolute;
    right: 15px;
    top: 15px;
    z-index: 3;
    opacity: .4;
}
.tb_gp_social__ico{
    color: var(--tb-icon-color);
}

.tb_gp_post_in:hover .tb_gp_social_ {
    opacity: 1;
}

.tb_gp_social__ico {
    font-size: 24px;
}

.tb_gp_content {
    text-align: center;
    white-space: pre-line;
    overflow: hidden;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 3;
    text-overflow: ellipsis;
    width: 100%;
}

.tb_gp_post_in:hover .tb_share_wrapper{
    opacity: 1;
    visibility: visible;
}

.tb_gp_media_icon.tb__media_ico_ .tb__icon{
    opacity: 1;
}

.tb_gp_media_icon.tb__media_ico_ .tb__icon:nth-child(1) {
    right: auto;
    left: var(--tb-icon-position);
    top: var(--tb-icon-position);
}

.tb_gp_media_icon.tb__media_ico_ .tb__icon:nth-child(2) {
    right: auto;
    left: var(--tb-icon-position);
    top: auto;
    bottom: var(--tb-icon-position);
}

.tb_gp_media_icon.tb__media_ico_ .tb__icon:nth-child(3) {
    left: auto;
    right: var(--tb-icon-position);
    top: auto;
    bottom: var(--tb-icon-position);
}

.tb_gp_post_wrapper2x .tb_gp_content,
.tb_gp_post_wrapper2x .tb_gp_post_info,
.tb_gp_post_wrapper1x .tb_gp_social_,
.tb_gp_post_wrapper1x .tb_gp_contant_,
.tb_gp_post_wrapper1x .tb_gp_video_ico {
    display: none;
}

.tb_gp_post_wrapper2x .tb_gp_author_info{
    margin-bottom: 0px;
}

.tb_gp_post_wrapper2x .tb_gp_authorname {
    font-size: 10px;
}

.tb_gp_post_wrapper2x .tb_gp_contant__in{
    padding: 5px;
}

.tb_gp_post_wrapper2x .tb_gp_author_profile {
    height: 40px;
    width: 40px;
}

.tb_gp_post_wrapper2x .tb_gp_author_profile{
    margin-bottom: 5px;
}

.tb_gp_post_wrapper2x .tb_gp_social__ico {
    font-size: 18px;
}

.tb_gp_post_wrapper2x .tb_gp_video_ico {
    font-size: 10px;
}

.tb_gp_post_wrapper2x .tb_gp_video_ico, 
.tb_gp_post_wrapper2x .tb_gp_audio_ico {
    left: 10px;
    top: 10px;
}

.tb_gp_post_wrapper2x .tb_gp_social_ {
    right: 10px;
    top: 10px;
}

.tb_gp_post_wrapper2x .tb_gp_contant_ {
    padding: 5px;
}